import React from 'react'

const AddUpvotes = ({articleName , upvotes , setArticleInfo}) => {

    const upvoteArticle = async () => {
        const result = await fetch(`/api/article/${articleName}/upvote`, {
            method : 'post',
        });
        const body = await result.json()
        setArticleInfo(body)
    }

    return (
        <>
            <div>
                <button className="upvoteButton" onClick={() => upvoteArticle()}>Upvote</button>
                This article has been upvoted {upvotes} times.
            </div>
            
        </>
    );
    
}

export default AddUpvotes;