import React , {useState,useEffect} from 'react'
import articleContent from './article-content'
import ArticleList from '../components/ArticleList'
import CommentsList from '../components/CommentsList'
import NotFoundPage from './NotFoundPage'
import AddUpvotes from '../components/AddUpvotes'
import AddComment from '../components/AddComment'
import '../App.css';

const ArticlePage = ({match}) => {

    const name = match.params.name
    const article = articleContent.find(article => article.name === name)

    const otherArticles = articleContent.filter(article => article.name !== name)

    const [articleInfo,setArticleInfo] = useState({
        upvotes : 0,
        comments : []
    })
    
    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/article/${name}`)
            const body = await result.json()
            setArticleInfo(body)
        }
        fetchData()
    },[name])

    if(!article) {
        return (
            <NotFoundPage/>
        )
    }else
    return(
        <>
        <div className="article-body">

            <h1>This is the {name} page</h1>
            <AddUpvotes articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo}/>
            {article.content.map((para,key) => (
                <p key={key}>
                    {para}
                </p>
            ))}

            <CommentsList comments={articleInfo.comments} />

            <AddComment articleName={name} setArticleInfo={setArticleInfo} />

            <h3>
                Other Articles
            </h3>
            <ArticleList articles={otherArticles} />
        </div> 
            
        </>
    )
}

export default ArticlePage;