import React,{useState} from 'react'

const AddComment = ({articleName,setArticleInfo}) => {

    const [username,setUsername] = useState('')
    const [comment,setComment] = useState('')

    const submitComment = async () => {
        console.log('comment submitted')
        const result = await fetch(`/api/article/${articleName}/add-comment/`,{
            method : 'post',
            body : JSON.stringify({username , text : comment}),
            headers : {
                'Content-Type' : 'application/json'
            }
        })
        const body = await result.json()
        setArticleInfo(body)
        setUsername('')
        setComment('')
    }

    return(
        <>
            <div className="comment-form">
                <h3> Add a comment</h3>
                <label>
                    Username :
                    <input type='text' value={username} onChange={(event)=>setUsername(event.target.value)}></input>
                </label>
                <br></br>
                <label>
                    Comment :
                    <textarea rows="4" cols="50" value={comment} onChange={(event)=>setComment(event.target.value)}></textarea>
                </label>
                <button className="commentButton" onClick={()=>submitComment()}>Submit</button>
            </div>
        </>

    )

}

export default AddComment